import react from "react";
import { useEffect } from "react";
import './App.css'
import searchIcon from './search.svg'
import MovieCard from './MovieCard'
import { useState } from "react";

// 4a0c9636      http://www.omdbapi.com/?apikey=[yourkey]&

const API_URL = 'http://www.omdbapi.com?apikey=4a0c9636'

const movie = {
    
        "Title": "Superman Returns",
        "Year": "2006",
        "imdbID": "tt0348150",
        "Type": "movie",
        "Poster": "https://m.media-amazon.com/images/M/MV5BNzY2ZDQ2MTctYzlhOC00MWJhLTgxMmItMDgzNDQwMDdhOWI2XkEyXkFqcGdeQXVyNjc1NTYyMjg@._V1_SX300.jpg"
    
}

const App = () =>{

    const [movies, setMovies] = useState([])
    const [searchTerm, setSearchTerm] = useState('')

  const searchMovies = async (title) => {
      const response = await fetch(`${API_URL}&s=${title}`)
      const data = await response.json()

      setMovies(data.Search)
    //   console.log(data.totalResults)
  }
    useEffect(() => {
        searchMovies('ip man')
    }, [])

    return( <>      
        <div className="app">
            <h1>MovieTime</h1>
        </div>
        <div className="search-for-movie">Search for a movie below</div>
        <div className="search">
            <input 
            placeholder="search for movies"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}/>
            <img src={searchIcon} alt='search' 
            onClick={()=> searchMovies(searchTerm)}/>
        </div>

{
    movies?.length > 0
     ? (
        <div className="container">
            {movies.map((movie)=> (  <MovieCard movie={movie} /> ))}</div>
    ) : 
    (
        <div className="empty">
            <h2>No movies found</h2>
        </div>
     )
}

  

    
</> )
} 

export default App